:root {
  --bg: #2f323f;
}

body {
  min-height: 100vh;
}

.navigation {
  position: fixed;
  inset: 0px 0 0px 0px;
  background-color: #fff;
  max-width: 8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
}

.navigation.active {
  max-width: 250px;
}
.navigation.active .menu-toogle .logo {
  margin-left: 5rem;
  display: block;
  width: 30rem;
}

.menu-toogle {
  position: absolute;
  top: 0;
  left: 0;
  max-width: 100%;
  padding: 0 23px;
  height: 8rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: start;
  align-items: center;
  cursor: pointer;
}
.menu-toogle .logo {
  display: none;
}

.menu-toogle::before {
  content: "";
  position: absolute;
  width: 30px;
  height: 2px;
  background-color: #333;
  transform: translateY(-8px);
  transition: 0.5s;
}

.navigation.active .menu-toogle::before {
  transform: translateY(0) rotate(45deg);
}

.menu-toogle::after {
  content: "";
  position: absolute;
  width: 30px;
  height: 2px;
  background-color: #333;
  transform: translateY(8px);
  box-shadow: 0 -8px 0 #333;
  transition: 0.5s;
}

.navigation.active .menu-toogle::after {
  transform: translateY(0) rotate(-45deg);
  box-shadow: 0 0 0 #333;
}

.navigation ul {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.navigation ul li {
  list-style: none;
  position: relative;
  width: 100%;
  height: 80px;
  border-radius: 12px;
  border: 8px solid transparent;
  transition: 0.5s;
}

.navigation ul li.active {
  transform: translateX(30px);
  background-color: var(--bg);
}

.navigation ul li::before {
  content: "";
  top: -28px;
  right: -10px;
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border-bottom-right-radius: 20px;
  box-shadow: 6px 5px 0 5px var(--bg);
  transform: scale(0);
  transform-origin: bottom right;
  transition: 0.5s;
}

.navigation ul li.active::before {
  right: 22px;
  transform: scale(1);
}

.navigation ul li::after {
  content: "";
  bottom: -28px;
  right: -10px;
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border-top-right-radius: 20px;
  box-shadow: 6px -3px 0 3px var(--bg);
  transform: scale(0);
  transform-origin: bottom right;
  transition: 0.5s;
}

.navigation ul li.active::after {
  right: 22px;
  transform: scale(1);
}

.navigation ul li a {
  position: relative;
  display: flex;
  justify-content: start;
  align-items: center;
  max-width: 100%;
  text-align: center;
  text-decoration: none;
  z-index: 1000;
}
.navigation ul li a:hover {
  color: var(--clr);
}

.navigation ul li a .icon {
  color: black;
  position: relative;
  display: block;
  min-width: 60px;
  height: 60px;
  border-radius: 12px;
  font-size: 3.7rem;
  line-height: 60px;
  border: 6px solid transparent;
  transition: 0.5s;
}
.navigation ul li a .icon:hover {
  color: var(--clr);
}

.navigation ul li.active a .icon {
  color: #fff;
  background-color: var(--clr);
}

.navigation ul li a .icon::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--clr);
  filter: blur(8px);
  opacity: 0;
  transition: 0.5s;
}

.navigation ul li.active a .icon::before {
  opacity: 0.5;
}

.navigation ul li a .icon::after {
  content: "";
  position: absolute;
  top: 10px;
  left: -62px;
  width: 15px;
  height: 15px;
  background-color: var(--clr);
  border: 8px solid var(--bg);
  border-radius: 50%;
}

.navigation ul li a .text {
  position: relative;
  padding: 0 15px;
  color: #333;
  display: flex;
  align-items: center;
  height: 60px;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;
  font-family: 2rem;
}

.navigation.active ul li a .text {
  visibility: visible;
  opacity: 1;
}

.navigation ul li.active a .text {
  font-family: 2rem;
  color: #fff;
}

